import React, { Component } from "react";

class Experience extends Component {
  render() {
    return (
      <div>
        <div className="experience" id="experience">
          <h1 className="exp-header">Experience</h1>
          <ul className="exp-list">
            <li className="Jalali-Lab">
              <p className="exp-title">Jalali Lab @ UCLA</p>
              <p className="exp-body">
                I am currently a research assistant at UCLA's Jalali Lab,
                developing and utilizing computer vision algorithms to perform
                image segmentation on medical images of cells.
              </p>
            </li>

            <li className="Bruin-Racing">
              <p className="exp-title">Bruin Racing @ UCLA</p>
              <p className="exp-body">
                As a member of UCLA's Bruin Racing, I work on the Data Acquisition
                team to gather data about our full-electric vehicle. In April 2019, our team
                participated in the Shell Eco-Marathon Competition, competing against other schools across
                the world for the most energy effficient vehicle. 
              </p>
            </li>

            <li className="IEEE">
              <p className="exp-title">IEEE @ UCLA</p>
              <p className="exp-body">
                At UCLA's Institute of Electrical and Electronics Engineers Association,
                we build small projects to get hands on experience with hardware related
                to electrical engineering and utilize Arduinos to create gadgets such as
                games, piano keys, and an autonomous car.
              </p>
            </li>

            <li className="IdeaHacks">
              <p className="exp-title">IdeaHacks 2019</p>
              <p className="exp-body">
                As a participant in UCLA's 2019 IdeaHacks, my team and I created a Smart Lock
                with features including facial recognition, motion sensing, and light indicators
                with a solenoid lock, a Raspberry Pi, and our coding skills.
              </p>
          
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default Experience;
